import { SignJWT, jwtVerify } from 'jose';
import { cookies } from 'next/headers';
import { supabase } from './supabase';

const JWT_SECRET = new TextEncoder().encode(process.env.JWT_SECRET || 'fallback-secret');

export interface JWTPayload {
    userId: string;
    discordId: string;
    isAdmin: boolean;
}

export async function createSession(userId: string, discordId: string, isAdmin: boolean, ip?: string, userAgent?: string) {
    const token = await new SignJWT({ userId, discordId, isAdmin })
        .setProtectedHeader({ alg: 'HS256' })
        .setExpirationTime('7d')
        .sign(JWT_SECRET);

    const expiresAt = new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString();

    await supabase.from('sessions').insert({
        user_id: userId,
        token,
        ip,
        user_agent: userAgent,
        expires_at: expiresAt,
    });

    return token;
}

export async function verifySession(token: string): Promise<JWTPayload | null> {
    try {
        const { payload } = await jwtVerify(token, JWT_SECRET);

        console.log('verifySession - JWT payload:', payload);

        // Simple query without join
        const { data: session, error: sessionError } = await supabase
            .from('sessions')
            .select('*')
            .eq('token', token)
            .single();

        if (sessionError) {
            console.error('verifySession - Session query error:', sessionError);
            return null;
        }

        if (!session || new Date(session.expires_at) < new Date()) {
            console.log('verifySession - Session expired or not found');
            return null;
        }

        // Get user separately
        const { data: user, error: userError } = await supabase
            .from('users')
            .select('*')
            .eq('id', session.user_id)
            .single();

        if (userError || !user) {
            console.error('verifySession - User query error:', userError);
            return null;
        }

        console.log('verifySession - Success, user:', user.username);

        return {
            userId: session.user_id,
            discordId: user.discord_id,
            isAdmin: user.is_admin,
        };
    } catch (error) {
        console.error('verifySession - JWT or other error:', error);
        return null;
    }
}

export async function getCurrentUser() {
    const cookieStore = await cookies();
    const token = cookieStore.get('session')?.value;

    if (!token) return null;

    const payload = await verifySession(token);
    if (!payload) return null;

    const { data: user } = await supabase
        .from('users')
        .select('*')
        .eq('id', payload.userId)
        .single();

    return user;
}

export async function isUserAuthorized(userId: string): Promise<boolean> {
    const { data: user } = await supabase
        .from('users')
        .select('*')
        .eq('id', userId)
        .single();

    if (!user) return false;
    if (user.is_admin) return true;
    if (!user.is_authorized) return false;
    if (user.auth_expiry && new Date(user.auth_expiry) < new Date()) return false;

    return true;
}

export function getDiscordOAuthUrl() {
    const params = new URLSearchParams({
        client_id: process.env.DISCORD_CLIENT_ID!,
        redirect_uri: process.env.DISCORD_REDIRECT_URI!,
        response_type: 'code',
        scope: 'identify email',
    });

    return `https://discord.com/api/oauth2/authorize?${params.toString()}`;
}

export async function exchangeCodeForToken(code: string) {
    const response = await fetch('https://discord.com/api/oauth2/token', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: new URLSearchParams({
            client_id: process.env.DISCORD_CLIENT_ID!,
            client_secret: process.env.DISCORD_CLIENT_SECRET!,
            grant_type: 'authorization_code',
            code,
            redirect_uri: process.env.DISCORD_REDIRECT_URI!,
        }),
    });

    return response.json();
}

export async function getDiscordUser(accessToken: string) {
    const response = await fetch('https://discord.com/api/users/@me', {
        headers: {
            Authorization: `Bearer ${accessToken}`,
        },
    });

    return response.json();
}
