import { NextRequest, NextResponse } from 'next/server';
import { getCurrentUser } from '@/lib/auth';
import { supabase } from '@/lib/supabase';

// GET all users
export async function GET() {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { data: users } = await supabase
            .from('users')
            .select('id, discord_id, username, email, avatar, is_authorized, auth_expiry, is_admin, created_at')
            .order('created_at', { ascending: false });

        // Transform to camelCase for frontend
        const transformedUsers = (users || []).map(user => ({
            id: user.id,
            discordId: user.discord_id,
            username: user.username,
            email: user.email,
            avatar: user.avatar,
            isAuthorized: user.is_authorized,
            authExpiry: user.auth_expiry,
            isAdmin: user.is_admin,
            createdAt: user.created_at,
        }));

        return NextResponse.json({ users: transformedUsers });
    } catch (error) {
        console.error('Error fetching users:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

// POST authorize user
export async function POST(request: NextRequest) {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { userId, days } = await request.json();

        if (!userId) {
            return NextResponse.json({ error: 'User ID is required' }, { status: 400 });
        }

        const authExpiry = days > 0
            ? new Date(Date.now() + days * 24 * 60 * 60 * 1000).toISOString()
            : null;

        await supabase
            .from('users')
            .update({
                is_authorized: true,
                auth_expiry: authExpiry,
            })
            .eq('id', userId);

        return NextResponse.json({ success: true });
    } catch (error) {
        console.error('Error authorizing user:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

// DELETE revoke user authorization
export async function DELETE(request: NextRequest) {
    try {
        const currentUser = await getCurrentUser();

        if (!currentUser?.is_admin) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
        }

        const { userId } = await request.json();

        if (!userId) {
            return NextResponse.json({ error: 'User ID is required' }, { status: 400 });
        }

        await supabase
            .from('users')
            .update({
                is_authorized: false,
                auth_expiry: null,
            })
            .eq('id', userId);

        return NextResponse.json({ success: true });
    } catch (error) {
        console.error('Error revoking user:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}
